import React, { useState } from 'react';

const TechnicalCheckStep = ({ onComplete, onBack, userData }) => {
  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [answers, setAnswers] = useState([]);
  const [selectedAnswer, setSelectedAnswer] = useState(null);
  const [showExplanation, setShowExplanation] = useState(false);

  const questionsByLevel = {
    basico: [
      {
        id: 'q1_basic',
        question: 'Cul de estas herramientas es ms similar a Power BI?',
        options: [
          { id: 'a', text: 'Microsoft Word', correct: false },
          { id: 'b', text: 'Tablas dinmicas de Excel', correct: true },
          { id: 'c', text: 'PowerPoint', correct: false },
          { id: 'd', text: 'Outlook', correct: false }
        ],
        explanation: 'Power BI es como las tablas dinmicas de Excel pero mucho ms potente. Ambas herramientas te permiten analizar y visualizar datos de forma dinmica.',
        points: 1
      },
      {
        id: 'q2_basic',
        question: 'En Excel usas grficos para mostrar datos. Cmo se llaman en Power BI?',
        options: [
          { id: 'a', text: 'Charts', correct: false },
          { id: 'b', text: 'Grficos', correct: false },
          { id: 'c', text: 'Visualizaciones o Visuals', correct: true },
          { id: 'd', text: 'Widgets', correct: false }
        ],
        explanation: 'En Power BI se llaman "visualizaciones" o "visuals". Hay muchos tipos: grficos de barras, lneas, mapas, tablas, KPIs y ms.',
        points: 1
      }
    ],
    intermedio: [
      {
        id: 'q1_inter',
        question: 'Cul es la diferencia principal entre una columna calculada y una medida?',
        options: [
          { id: 'a', text: 'No hay diferencia', correct: false },
          { id: 'b', text: 'Las columnas se calculan al cargar datos, las medidas al mostrar visualizaciones', correct: true },
          { id: 'c', text: 'Las medidas son siempre ms rpidas', correct: false },
          { id: 'd', text: 'Las columnas solo funcionan con texto', correct: false }
        ],
        explanation: 'Diferencia crtica: Columnas calculadas se almacenan en memoria (aumentan tamao del modelo). Medidas se calculan dinmicamente segn el contexto de la visualizacin.',
        points: 2
      },
      {
        id: 'q2_inter',
        question: 'Tienes una tabla de Ventas y otra de Productos. Qu cardinalidad tiene la relacin?',
        options: [
          { id: 'a', text: 'Uno a Uno (1:1)', correct: false },
          { id: 'b', text: 'Uno a Muchos (1:*)', correct: true },
          { id: 'c', text: 'Muchos a Muchos (*:*)', correct: false },
          { id: 'd', text: 'No se pueden relacionar', correct: false }
        ],
        explanation: 'Relacin 1:* es la ms comn: UN producto puede aparecer en MUCHAS ventas. Productos (lado uno)  Ventas (lado muchos).',
        points: 2
      }
    ],
    avanzado: [
      {
        id: 'q1_adv',
        question: 'Una medida con CALCULATE est dentro de un iterador SUMX. Qu ocurre?',
        options: [
          { id: 'a', text: 'Error de sintaxis', correct: false },
          { id: 'b', text: 'Context transition en cada iteracin', correct: true },
          { id: 'c', text: 'Se optimiza automticamente', correct: false },
          { id: 'd', text: 'Solo funciona con tablas pequeas', correct: false }
        ],
        explanation: 'Context transition ocurre cuando CALCULATE est dentro de un iterador. Esto convierte el row context en filter context, lo cual puede impactar significativamente el performance.',
        points: 3
      },
      {
        id: 'q2_adv',
        question: 'Tu modelo tiene 2GB y tarda mucho en refrescar. Qu revisas PRIMERO?',
        options: [
          { id: 'a', text: 'Comprar ms RAM', correct: false },
          { id: 'b', text: 'Columnas con alta cardinalidad y columnas calculadas innecesarias', correct: true },
          { id: 'c', text: 'Cantidad de visualizaciones', correct: false },
          { id: 'd', text: 'Theme del reporte', correct: false }
        ],
        explanation: 'Optimizacin 101: Columnas con valores nicos (alta cardinalidad) y columnas calculadas consumen mucha memoria. Elimina columnas innecesarias, usa medidas en lugar de columnas calculadas.',
        points: 3
      }
    ]
  };

  const questions = questionsByLevel[userData.experienceLevel] || questionsByLevel.basico;

  const handleSelectAnswer = (optionId) => {
    if (!showExplanation) {
      setSelectedAnswer(optionId);
    }
  };

  const handleConfirm = () => {
    const question = questions[currentQuestion];
    const selectedOption = question.options.find(opt => opt.id === selectedAnswer);
    
    const newAnswers = [
      ...answers,
      {
        questionId: question.id,
        selectedAnswer: selectedAnswer,
        correct: selectedOption.correct,
        points: selectedOption.correct ? question.points : 0
      }
    ];
    
    setAnswers(newAnswers);
    setShowExplanation(true);
  };

  const handleNext = () => {
    if (currentQuestion < questions.length - 1) {
      setCurrentQuestion(currentQuestion + 1);
      setSelectedAnswer(null);
      setShowExplanation(false);
    } else {
      const totalPoints = answers.reduce((sum, ans) => sum + ans.points, 0);
      const maxPoints = questions.reduce((sum, q) => sum + q.points, 0);
      const accuracy = (totalPoints / maxPoints) * 100;

      onComplete({
        technicalAnswers: answers,
        technicalScore: totalPoints,
        technicalAccuracy: accuracy
      });
    }
  };

  const question = questions[currentQuestion];
  const selectedOption = question.options.find(opt => opt.id === selectedAnswer);

  return (
    <div className="onboarding-step technical-step">
      <div className="step-header">
        <h2>Verificacin Tcnica Rpida</h2>
        <p className="step-description">
          Solo {questions.length} preguntas para afinar tu perfil
        </p>
      </div>

      <div className="question-counter">
        Pregunta {currentQuestion + 1} de {questions.length}
      </div>

      <div className="technical-question-card">
        <h3 className="question-text">{question.question}</h3>

        <div className="options-list">
          {question.options.map((option) => {
            const isSelected = selectedAnswer === option.id;
            const showCorrect = showExplanation && option.correct;
            const showIncorrect = showExplanation && isSelected && !option.correct;

            return (
              <button
                key={option.id}
                className={`option-button ${isSelected ? 'selected' : ''} ${showCorrect ? 'correct' : ''} ${showIncorrect ? 'incorrect' : ''}`}
                onClick={() => handleSelectAnswer(option.id)}
                disabled={showExplanation}
              >
                <span className="option-letter">{option.id.toUpperCase()}</span>
                <span className="option-text">{option.text}</span>
                {showCorrect && <span className="result-icon"></span>}
                {showIncorrect && <span className="result-icon"></span>}
              </button>
            );
          })}
        </div>

        {showExplanation && (
          <div className={`explanation-box ${selectedOption?.correct ? 'correct' : 'incorrect'}`}>
            <div className="explanation-header">
              {selectedOption?.correct ? (
                <>
                  <span className="explanation-icon"></span>
                  <strong>Correcto!</strong>
                </>
              ) : (
                <>
                  <span className="explanation-icon"></span>
                  <strong>Informacin til:</strong>
                </>
              )}
            </div>
            <p>{question.explanation}</p>
          </div>
        )}
      </div>

      <div className="step-actions">
        {!showExplanation ? (
          <>
            <button className="btn-secondary" onClick={onBack}>
               Atrs
            </button>
            <button
              className="btn-primary"
              onClick={handleConfirm}
              disabled={!selectedAnswer}
            >
              Confirmar Respuesta
            </button>
          </>
        ) : (
          <button className="btn-primary btn-large" onClick={handleNext}>
            {currentQuestion < questions.length - 1 ? 'Siguiente Pregunta' : 'Ver mi Perfil'}
            <span className="btn-arrow"></span>
          </button>
        )}
      </div>
    </div>
  );
};

export default TechnicalCheckStep;
